.bg-gray-100 {
  background-color: #f7fafc;
}

.p-6 {
  padding: 1.5rem;
}

.bg-blue-500 {
  background-color: #3b82f6;
}

.text-center {
  text-align: center;
}

.text-white {
  color: #ffffff;
}

.text-2xl {
  font-size: 1.5rem;
}

.mb-4 {
  margin-bottom: 1rem;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.justify-center {
  justify-content: center;
}

.items-start {
  align-items: flex-start;
}

.h-screen {
  height: 100vh;
}

.overflow-y-auto {
  overflow-y: auto;
}

.w-full {
  width: 100%;
}

.editor-container {
  height: calc(50vh - 6rem); /* Subtract header and footer heights */
  overflow: hidden;
  background-color: #1f2937;
  color: #ffffff;
}

.output-container {
  height: calc(50vh - 6rem); /* Subtract header and footer heights */
  overflow: hidden;
  background-color: #1f2937;
  color: #ffffff;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.px-4 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.bg-green-500 {
  background-color: #10b981;
}

.text-white {
  color: #ffffff;
}

.rounded-md {
  border-radius: 0.375rem;
}

.run-button {
  font-size: 1rem;
  position: absolute;
  left: 25px;
  top: 25px;
  margin-top: auto;
  margin-bottom: auto;
}

.main-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}

.editor-container {
  height: 100%;
  padding: 0.5rem;
  overflow: hidden;
}

.output-container {
  height: 100%;
  padding: 0.5rem;
  overflow: hidden;
}
.mx-auto {
  margin-left: auto;
  margin-right: auto;
}